<template>
  <div id="footer">
    <ul>
      
      <li @click="jumpPhoto">
        <img class="img" src="../../static/images/photo.png" alt="">
        <div>图片</div>
      </li>
      <li @click="jumpHome">
        <img src="../../static/images/vanke.png" alt="">
        <div>万科</div>
      </li>
      <!-- <li @click="jumpVideo">
        <img class="img1" src="../../static/images/video.png" alt="">
        <div>视频</div>
      </li> -->
      <li @click="jumpInfo">
        <img class="img1" src="../../static/images/sale.png" alt="">
        <div>信息</div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "Footer",
  data() {
    return {
      msg: "Welcome to Footer"
    };
  },
  created(){
  },
  methods:{
    jumpHome(){
      this.$router.push({name: 'Home',params:{}});
    },
    jumpPhoto(){
      this.$router.push({name: 'Photo',params:{}});
    },
    jumpVideo(){
      this.$router.push({name: 'Video',params:{}});
    },
    jumpInfo(){
      this.$router.push({name: 'Info',params:{}});
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #footer{
    position: fixed;padding:0.05rem 0;bottom:0;left:0;width:100%;background: #fff;overflow: hidden;border-top:1px solid #999;
  }
  #footer ul{
    display: flex;
    justify-content:space-around;
    
  }
  li{
    font-size: 0.12rem;
    text-align: center;
  }
  li img{
    width:0.3rem;
    height:0.3rem;
    margin-bottom: 0.05rem;
  }
</style>



